html, body {
  background-color: #fff;
  height: auto; }

body {
  font-size: 18px;
  font-family: Arial, sans-serif;
  font-weight: normal;
  margin: 0 auto;
  line-height: 1.3;
  width: 100%; }
  @media (min-width: 64em) {
    body {
      font-size: 16px; } }

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

.header {
  background: #72e912;
  vertical-align: middle; }
  .header__inner {
    display: flex;
    align-items: center;
    justify-content: space-around; }
    @media (min-width: 64em) {
      .header__inner {
        padding: 0 5%; } }

.languages {
  background: #fff;
  color: #000;
  text-align: center; }
  @media (min-width: 64em) {
    .languages {
      background: #72e912;
      text-align: right; } }
  @media (min-width: 64em) {
    .languages--wrapper {
      padding: 0 5%; } }
  .languages-list {
    margin: 0;
    padding: 0.9em; }
    @media (min-width: 64em) {
      .languages-list {
        padding: 10px; } }
    .languages-list__anchor {
      color: #000;
      display: block;
      text-decoration: none;
      font-size: 1.4em; }
      @media (min-width: 64em) {
        .languages-list__anchor {
          color: #007800;
          font-size: 1em; } }
      .languages-list__anchor:after {
        display: inline-block;
        color: #000;
        content: " | ";
        padding: 0 5px; }
      .languages-list__anchor--active {
        color: #8308ff; }
        @media (min-width: 64em) {
          .languages-list__anchor--active {
            color: #000; } }
      .languages-list__anchor:active, .languages-list__anchor:hover, .languages-list__anchor:focus {
        color: #8308ff; }
        @media (min-width: 64em) {
          .languages-list__anchor:active, .languages-list__anchor:hover, .languages-list__anchor:focus {
            color: #000; } }
      @media (min-width: 64em) {
        .languages-list__anchor:visited {
          outline: none; } }
    .languages-list__item {
      display: inline-block; }
      .languages-list__item:last-child .languages-list__anchor:after {
        display: none; }
